import React,{useState} from 'react'
import "./style.css"

const Header=()=>{
    let stick={position: "sticky",
               top: 0}
    return(
        <header className="text-gray-500 bg-gray-900 body-font" style={stick}>
        <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
            <nav className="flex lg:w-2/5 flex-wrap items-center text-base md:ml-auto">
            <a className="mr-5 hover:text-white">First Link</a>
            <a className="mr-5 hover:text-white">Second Link</a>
            <a className="mr-5 hover:text-white">Third Link</a>
            <a className="hover:text-white">Fourth Link</a>
            </nav>
            <a className="flex order-first lg:order-none lg:w-1/5 title-font font-medium items-center text-white lg:items-center lg:justify-center mb-4 md:mb-0">
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" className="w-10 h-10 text-white p-2 bg-purple-500 rounded-full" viewBox="0 0 24 24">
                <path d="M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5"></path>
            </svg>
            <span className="ml-3 text-xl xl:block lg:hidden">tailblocks</span>
            </a>
            <div className="lg:w-2/5 inline-flex lg:justify-end ml-5 lg:ml-0">
            <button className="inline-flex items-center bg-gray-800 border-0 py-1 px-3 focus:outline-none hover:bg-gray-700 rounded text-base mt-4 md:mt-0">Button
            </button>
            </div>
        </div>
        </header>
    )
}



const Card=()=>{
    return(
        <div class="xl:w-1/4 md:w-1/2 p-4">
        <div class="bg-gray-800 p-6 rounded-lg">
        <img class="h-40 rounded w-full object-cover object-center mb-6" src="https://dummyimage.com/723x403" alt="content"/>
        <h3 class="tracking-widest text-purple-500 text-xs font-medium title-font">SUBTITLE</h3>
        <h2 class="text-lg text-white font-medium title-font mb-4">San Francisco</h2>
        <p class="leading-relaxed text-base">Fingerstache flexitarian street art 8-bit waistcoat. Distillery hexagon disrupt edison bulbche.</p>
        </div>
        </div>
    )
}

